import Vue from "vue";
import Vuex from "vuex";
import router from "./router";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        tema: false,
        navegacion: true,
        seccionesBusqueda: [],
        titulos: [],
        secciones: [{
                icono: 'local_activity',
                titulo: 'Administrador',
                items: [
                    { titulo: 'Empleados', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Usuarios', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Modelos', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Cantidad por modelo', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Detalle de nómina', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Datos de la empresa', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Mi cuenta', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Tutoriales', icono: 'local_activity', enlace: '/' }
                ]
            },
            {
                icono: 'local_activity',
                titulo: 'Corte',
                items: [
                    { titulo: 'Listado de telas', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Secciones', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Inventario de telas', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Gestion de inventario', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Listado de colores', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Tutoriales', icono: 'local_activity', enlace: '/' }
                ]
            },
            {
                icono: 'local_activity',
                titulo: 'Confección',
                items: [
                    { titulo: 'Operaciones', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Maquinas', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Operaciones por modelo', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Defectos', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Nómina', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Tutoriales', icono: 'local_activity', enlace: '/' }
                ]
            },
            {
                icono: 'local_activity',
                titulo: 'Terminado',
                items: [
                    { titulo: 'Seguimiento de modelo', icono: 'local_activity', enlace: '/' }
                ]
            },
            {
                icono: 'local_activity',
                titulo: 'Almacén',
                items: [
                    { titulo: 'Tallas', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Tipo de producto', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Secciones', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Almacén de prendas', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Gestión de almacén', icono: 'local_activity', enlace: '/' },
                    { titulo: 'Tutoriales', icono: 'local_activity', enlace: '/' }
                ]
            }
        ],
        notificaciones: [
            { texto: 'Notificacion 1', estado: true, fecha: '01-01-2019 - 12:00' },
            { texto: 'Notificacion 2', estado: false, fecha: '01-01-2019 - 12:00' },
            { texto: 'Notificacion 3', estado: true, fecha: '01-01-2019 - 12:00' },
            { texto: 'Notificacion 4', estado: false, fecha: '01-01-2019 - 12:00' }
        ]
    },
    mutations: {
        cambiarTema(state) {
            state.tema = !state.tema;
            localStorage.setItem('besa-config', JSON.stringify(state.tema));
        },
        generarFecha() {
            let fecha = new Date();
            let fechaGenerada = fecha.getUTCDay() + '/' + fecha.getUTCMonth() + '/' + fecha.getUTCFullYear() + ' - ' + fecha.getUTCHours() + ':' + fecha.getUTCMinutes();
            return fechaGenerada;
        },
        guardarNotificacion(state) {
            state.notificaciones.push({
                texto: texto,
                estado: estado,
                fecha: this.generarFecha()
            });
            this.guardarlocal();
        },
        borrarNotificacion(state, index) {
            state.notificaciones.splice(index, 1);
        },
        borrarNotificaciones(state) {
            localStorage.removeItem('besa-notificaciones');
            state.notificaciones = [];
        },
        guardarlocal(state) {
            localStorage.setItem('besa-notificaciones', JSON.stringify(state.notificaciones));
        },
        leerLocal(state) {
            let datos = JSON.parse(localStorage.getItem('besa-notificaciones'));
            if (datos == null) {
                state.notificaciones = [];
            } else {
                state.notificaciones = datos;
            }
        },
        leerSecciones(state) {
            for (let index = 0; index < state.secciones.length; index++) {
                for (let seccion of state.secciones[index].items) {
                    state.seccionesBusqueda.push({
                        titulo: seccion.titulo,
                        icono: seccion.icono
                    });
                    state.titulos.push((seccion.titulo).toString());
                }
            }
        },
        redirigir(state, busqueda) {
            if (busqueda != null || busqueda != undefined) {
                let ruta = busqueda.toString().toLowerCase().replace(/ /g, '-');
                router.push(`/${ruta}`);
            }
        },
        ocultarNavegacion(state) {
            state.navegacion = false;
        },
        mostrarNavegacion(state) {
            router.push('/home');
            state.navegacion = true;
        }
    },
    actions: {}
});