<template>
  <v-app :dark="tema">
    <v-content>
      <router-view/>
    </v-content>
    <navegacion v-if="navegacion"></navegacion>
  </v-app>
</template>

<script>
import Navegacion from '@/components/Navegacion.vue';
import { mapState, mapMutations } from 'vuex';

export default {
  name: 'App',
  data () {
    return {
      //
    }
  },
  components: {
    Navegacion
  },
  computed: {
    ...mapState(['tema', 'navegacion'])
  },
  methods: {
    ...mapMutations(['cambiarTema']),
    obtenerConfig() {
      let config = JSON.parse(localStorage.getItem('besa-config'));
      if(config) {
        this.cambiarTema();
      }
    }
  },
  created() {
    this.obtenerConfig();
  },
}
</script>
